import { createRouter, createWebHistory } from 'vue-router'
const routes = [
    {
        path: '/Map_Demo/',
        name: 'index',
        component: () => import('@/view/Index.vue'),
    },
    {
        path: '/Map_Demo/map',
        name: 'map',
        component: () => import('@/view/WebMap.vue'),
    },
    {
        path: '/Map_Demo/mapDetails/:id',
        name: 'detail',
        component: () => import('@/view/MapDetails.vue')
    },
    {
        path: '/Map_Demo/Wes',
        name: 'Wes',
        component: () => import('@/view/Wes.vue')
    },
    {
        path: '/projectPlane/:id',
        name: 'projectPlane',
        component: () => import('@/view/projectPlane.vue')
    },
    // {
    //     path: '/Map_Demo/opp',
    //     name: 'opp',
    //     component: () => import('@/components/opp.vue')
    // },
]

const router = createRouter({
    history: createWebHistory(),
    routes
})
router.beforeEach((to, from, next) => {
    if (!to.name){
        next({ name: 'map' }) // 
    } else {
        next() // 
    }
})

export default router
